<template>
  <mu-container>
    <mu-form ref="form" :model="validateForm" class="mu-demo-form">
      <mu-form-item label="学号" help-text="帮助文字" prop="username" :rules="usernameRules">
        <mu-text-field v-model="validateForm.username" prop="username"></mu-text-field>
      </mu-form-item>
      <mu-form-item label="新密码" prop="password" :rules="passwordRules">
        <mu-text-field type="password" v-model="validateForm.password" prop="password"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="isAgree" :rules="argeeRules">
        <mu-checkbox label="同意用户协议(暂无)" v-model="validateForm.isAgree"></mu-checkbox>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" @click="submit">提交</mu-button>
        <mu-button @click="clear">重置</mu-button>
      </mu-form-item>
    </mu-form>
  </mu-container>
</template>

<script>
export default {
  name: 'ChangePassword',
  data () {
    return {
      usernameRules: [
        { validate: (val) => !!val, message: '必须填写学号' },
        { validate: (val) => val.length >= 5, message: '学号长度太短' }
      ],
      passwordRules: [
        { validate: (val) => !!val, message: '必须填写新密码' },
        { validate: (val) => val.length >= 1, message: '新密码长度大于1' }
      ],
      argeeRules: [{ validate: (val) => !!val, message: '必须同意用户协议' }],
      validateForm: {
        username: '',
        password: '',
        isAgree: false
      }
    }
  },
  methods: {
    submit () {
      this.$refs.form.validate().then((result) => {
        if (result) {
          console.log('提交')
          this.sendMessage()
        } else {
          console.log('表单不全')
        }
      })
    },
    clear () {
      this.$refs.form.clear()
      this.validateForm = {
        username: '',
        password: '',
        isAgree: false
      }
    },
    sendMessage () {
      let self = this
      self.$axios.post('/api/v1/official/changePassword', {
        username: self.validateForm.username,
        password: self.validateForm.password
      })
        .then(function (response) {
          if (response.data.ecode === 0) {
            self.$alert('密码修改成功', '提示', {
              okLabel: '知道了'
            })
          } else {
            self.$alert('密码修改失败,请检查账号是否有误或联系作者', '提示', {
              okLabel: '知道了'
            })
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}
</script>

<style scoped>
  .mu-demo-form {
    margin-top: 30px;
    text-align: left;
    width: 100%;
    /*max-width: 460px;*/
  }
</style>
